<template>
  <div :style="imgStyle">
    <v-upload :name="name" :action="action" drag @change="imgChange" :data="data" :accept="accept">
      <v-icon size="100">{{icon}}</v-icon>
      <p>{{text}}</p>
    </v-upload>
  </div>
</template>
<script>
export default {
  name: "UploadFile",
  props: {
    name: {
      type: String,
      default: "fileInfoOne"
    },
    action: {
      type: String,
      required: true
    },
    icon: {
      type: String,
      default: "mdi-plus"
    },
    data: {
      type: Object,
      default: () => ({})
    },
    text: {
      type: String,
      default: '请上传文件'
    },
    accept: {
      type: String,
      default: "-"
    },
    multiple: {
        type: Boolean,
        default: false
    },
    imgChange: {
      type: Function,
      default: fileInfo => {
        if (fileInfo.file.status !== "uploading") {
          console.log(fileInfo.file, fileInfo.fileList);
        }
        if (fileInfo.file.status === "done") {
          console.log(fileInfo.file.name + " 上传成功.");
        } else if (fileInfo.file.status === "error") {
          console.log(fileInfo.file.name + " 上传失败.");
        }
      }
    },
    imgStyle: {
      type: Object,
      default: () => {
        return {
          width: "100%",
          height: "20rem"
        };
      }
    }
  },
  data: () => ({}),
  methods: {
    onChange() {
      alert(1);
    }
  }
};
</script>